@tailwind base;
@tailwind components;
@tailwind utilities;
* {
  margin: 0;
  padding: 0;
  position: relative;
  scroll-behavior: smooth !important;
}
*::selection {
  background-color: rgba(51, 51, 51, 0.6666666667);
}

#root {
  overflow-x: hidden;
  --swiper-navigation-size: 0px !important;
}
#root::-webkit-scrollbar {
  display: none;
}

.Loader .AFill {
  animation: Fade 900ms ease-in infinite alternate-reverse forwards;
}
.Loader .progress {
  animation: Slide 900ms ease-in infinite alternate-reverse forwards;
}

@keyframes Fade {
  from {
    opacity: 100%;
  }
  to {
    opacity: 1%;
  }
}
@keyframes Slide {
  from {
    width: 1%;
  }
  to {
    width: 100%;
  }
}
#projects .bg,
#about .bg,
#contact .bg {
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-attachment: local;
  height: 100vh;
  width: 100%;
  background-size: auto 100%;
  background-position: top;
}

@font-face {
  font-family: montLight;
  src: url("../FONTS/Montserrat-Light.ttf");
}
@font-face {
  font-family: montXBold;
  src: url("../FONTS/Montserrat-ExtraBold.ttf");
}
@keyframes toRightFade {
  0% {
    transform: translateX(0%);
    opacity: 1;
  }
  40% {
    opacity: 0.03;
  }
  50% {
    transform: translateX(45%);
    opacity: 0;
  }
  60% {
    opacity: 0.03;
  }
  100% {
    transform: translateX(0%);
    opacity: 1;
  }
}
@keyframes fadeIn {
  0% {
    transform: translateY(100%) scaleY(0);
  }
  100% {
    transform: translateY(0%) scaleY(1);
  }
}
@keyframes bounce {
  0% {
    transform: translateY(0%);
  }
  50% {
    transform: translateY(50%);
  }
  100% {
    transform: translateY(0%);
  }
}
@keyframes effect {
  0% {
    transform: scale(1);
    box-shadow: 0 0 15px 1px transparent;
  }
  50% {
    transform: scale(0.975);
    box-shadow: 0 0 15px 1px #e3f2f8;
  }
  100% {
    transform: scale(1.005);
    box-shadow: 0 0 17px 0.5px #f5f5f9;
  }
}
.Loader {
  @apply w-full h-[100vh];
}
.Loader > div {
  @apply m-auto w-fit h-fit mt-[40vh];
}
.Loader > div svg {
  @apply w-[12vw] mx-5;
}
.Loader .progress {
  @apply h-1 w-[3rem] bg-gray-900 rounded my-7 transition-all duration-100 ease-in-out;
}

